.cart-goods {
  display: flex;
  margin: 0 -20px;
  perspective: 200px;
  flex-wrap: wrap;
  justify-content: center;
  &__elem {
    max-width: 300px;
    padding: 9px;
    background-color: #f1f1f1;
    border-radius: 10px;
    margin: 0 20px;
    box-shadow: 0px 5px 10px;
    transform: scale(1);
    transition: transform 0.25s linear, box-shadow 0.25s linear;
    flex-grow: 1;
    margin-bottom: 25px;
    &:hover {
      transform: scale(1.01);
      box-shadow: 0px 12px 18px;
    }
  }
  &__image-zone {
    background-color: #fff;
    border-radius: 10px;
    margin-bottom: 12px;
    width: 300px;
    height: 300px;
  }
  &__desc {
    text-align: center;
    margin-bottom: 20px;
  }
  &__h4 {
    font-size: 30px;
    line-height: 35px;
    font-weight: 500;
    font-family: "Roboto", sans-serif;
    margin: 0;
    margin-bottom: 15px;
  }
  &__text {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    width: 100%;
    color: #000;
  }
  &__action {
    display: flex;
    align-items: center;
  }
  &__cost {
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 24px;
    line-height: 35px;
    text-align: center;
    margin-left: 8px;
    min-width: 100px;
    white-space: nowrap;
  }
  &__select {
    display: flex;
    border-radius: 10px;
    padding: 3px;
    background-color: #fff;
    width: 50%;
    margin-right: 10px;
  }
  &__reveal {
    font-family: "Roboto";
    font-weight: 400px;
    font-size: 30px;
    line-height: 35px;
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border: none;
    display: inline-block;
    max-width: 40px;
    text-align: center;
  }
  &__change-select {
    display: inline-block;
    width: 50%;
  }
  &__btn-change {
    background-color: #bfbfbf;
    border-radius: 4px;
    padding: 7px 3px;
    border: none;
    outline: none;
    width: 100%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.25s linear;
    &:first-child {
      margin-bottom: 3px;
    }
    &:hover {
      background-color: #a3a3a3;
    }
    &:active {
      background-color: #858585;
    }
  }
  &__bag {
    cursor: pointer;
    padding: 0;
    border: none;
    outline: none;
    &:hover {
      .cart-goods__bag-svg {
        fill: #6a111c;
      }
    }
    &:active {
      .cart-goods__bag-svg {
        fill: #550d16;
      }
    }
  }
  &__bag-svg {
    fill: #8f1625;
    transition: fill 0.25s linear;
  }
  &__bag-image {
    min-width: 100%;
    width: auto;
    object-fit: cover;
    height: 100%;
  }
  &__picture {
    width: 100%;
    height: 100%;
    object-fit: unset;
    border-radius: 15px;
  }
}

@media screen and (max-width: 998px) {
  .cart-goods {
    display: none;
  }
}

@media screen and (max-width: 320px) {
  .cart-goods {
    &__elem {
      margin-left: 0;
      margin-right: 0;
    }
  }
}
